@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
.wrapper {
  width: 300px;
  height: 540px;
  padding: 8px;
  border-radius: 10px;
  background-color: #f1ee03;
}

.screen {
  height: 80px;
  width: 100%;
  margin-bottom: 20px;
  padding: 0 10px;
  background-color: #000000;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  color: rgb(160, 1, 1);
  font-weight: bold;
  box-sizing: border-box;
}

.buttonBox {
  width: 100%;
  height: calc(100% - 120px);
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: repeat(6, 1fr);
  grid-gap: 9px;
  margin-top: 40px;
}
button {
  border: none;
  background-color: rgb(34, 34, 34);
  font-size: 28px;
  color: rgb(209, 22, 22);
  font-weight: bold;
  cursor: pointer;
  border-radius: 6px;
  outline: none;
}

button:hover {
  /* background-color: rgb(61, 43, 184); */
}

.equals {
  grid-column: 1/6;
  background-color: rgb(243, 61, 29);
  position: relative;
  top: -15px;
}

.equals:hover {
  background-color: rgb(228, 39, 15);
}
* {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
}

body {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #000000;
}

